<template>
  <v-main>
    <div>
      <v-img class="bg-img" src="@/assets/product_bg.jpg" />
    </div>

    <div class="text-center">
      <p class="pa-8 pb-6 mb-0 text-h4">產品資訊</p>
    </div>

    <div class="pt-8"></div>

    <div class="top-btn d-none d-md-flex">
      <v-btn color="white" elevation="6" icon fab @click="toTop()">
        <v-icon color="black">mdi-chevron-up</v-icon>
      </v-btn>
    </div>

    <v-card class="mx-auto text-center" width="90%" flat>
      <p>全部產品資訊</p>
      <div class="line-black"></div>
      <div class="pt-8"></div>
      <v-row>
        <v-col
          class="pb-12"
          cols="12"
          md="3"
          v-for="item in productList"
          :key="item.name"
        >
          <v-hover v-slot="{ hover }">
            <v-card class="mx-auto" color="grey lighten-5" width="85%" outlined>
              <v-img :aspect-ratio="12 / 9" :src="productImg(item.image)">
                <v-fade-transition>
                  <v-overlay v-if="hover" absolute>
                    <v-btn :to="`/productlist/${item.name}`">more</v-btn>
                  </v-overlay>
                </v-fade-transition>
              </v-img>
              <v-card-text class="pt-6 px-0 text-center">
                <h3 class="text-h5 font-weight-light mb-2">
                  {{ item.name }}
                </h3>
              </v-card-text>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
    </v-card>

    <div class="top-btn-mobile d-flex d-md-none">
      <v-btn color="white" elevation="6" icon @click="toTop()">
        <v-icon color="black">mdi-chevron-up</v-icon>
      </v-btn>
    </div>
  </v-main>
</template>

<script>
export default {
  name: "Home",
  data: () => ({
    productList: [
      {
        image: "product",
        name: "Line投注1",
      },
      {
        image: "product",
        name: "Line投注2",
      },
      {
        image: "product",
        name: "Line投注3",
      },
      {
        image: "product",
        name: "Line投注4",
      },
      {
        image: "product",
        name: "Line投注5",
      },
      {
        image: "product",
        name: "Line投注6",
      },
      {
        image: "product",
        name: "Line投注7",
      },
      {
        image: "product",
        name: "Line投注8",
      },
      {
        image: "product",
        name: "Line投注9",
      },
      {
        image: "product",
        name: "Line投注10",
      },
    ],
  }),
  methods: {
    productImg(img) {
      img = require(`@/assets/${img}.jpg`);
      return img;
    },
    toTop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>

<style lang="css" scoped>
.about-text {
  width: 50%;
}

.line-black {
  background-color: black;
  height: 1px;
  width: 100%;
}

.top-btn {
  position: fixed;
  top: 80%;
  left: 95%;
}

@media screen and (max-width: 1100px) {
  .btn-list {
    width: 50px;
  }
}

@media screen and (max-width: 900px) {
  .about-text {
    width: 80%;
  }
}

@media screen and (max-width: 600px) {
  .fb-img {
    width: 80px;
  }

  .top-btn-mobile {
    position: fixed;
    top: 80%;
    left: 85%;
  }
}
</style>
